
.indexWrapper{
    width: 100%;
    display: flex;
}

.customizeShadow{
    width: 100%;
    height: 50px;
    /*border: 1px solid red;*/
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 40px 2.5% ;
}

.customizeShadow .p{
    font-weight: 600;
    font-size: 20px;
}

.customizeShadow .addBtn{
    background-color: #2563eb;;
    color: white;
    outline: none;
    border: none;
    line-height: 1.5rem;
    font-size: 1rem;
    padding: 0.25rem 0.75rem;
    border-radius: 0.25rem;
}

.LeftInputsWrapper{
    width: 100%;
    height: auto;
    /*border: 1px solid red;*/
    padding: 2.5%
}



.inputsDiv{
    width: 100%;
    margin-bottom: 10px;
}
.inputs_info{
    width: 100%;
    /*border: 1px solid black;*/
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
}

.input_size_units{
    display: flex;
    align-items: center;
    justify-content: center;
}
.input_size.color{
    max-width: 150px;
    width: fit-content;
    padding: 0 5px;
}

.input_size{
    width: 55px;
    height: 37px;
    background-color: rgb(243, 244, 246);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 3px;
}

.leftInputs{
    width: 100%;
    margin: 10px 0;
    appearance: none;
    background: #e5e7eb;
    cursor: pointer;
    height: 3px;
    position: relative;
    z-index: 10;
    border-radius: 1rem;

}

.leftInputs:before{
    content: "";
    position: absolute;
    width: 2px;
    height: 20px;
    background-color: #a5a5a8;
    left: 50%;
    transform: translate(-50%);
    top: -8px;
    z-index: -1;
}

.colorInput{
    width: 100%;
    height: 50px;
    border-radius: 0.5rem;
    font-size: 20px;
    letter-spacing: 1px;
    outline: none;
    text-indent: 10px;
    border: 1px solid rgba(128,128,128,0.5);
}


.mainIndex{
    width: 25%;
    max-height: 90vh;
    overflow: auto;
    /*padding: 10px 10px;*/
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    border: 1px solid rgba(128,128,128,0.5);
    border-top: none;
}

.leftInputs_properties_name{
    font-weight: bold;
}

.leftInputs_shadow{
    padding: 10px 0;
}


.exactShadow{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2.5%;
    border-bottom: 1px solid rgba(128,128,128,0.5);
}

.exactShadow:hover{
    background: rgba(128,128,128,0.5);
}


.checkboxDiv{
    width: 100%;
    /*border: 1px solid blue;*/
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.removeBtn{
    padding: 0.25rem 0.75rem;
    background-color: red;
    color: white;
    border-radius: 0.25rem;
    border: none;
    outline: none;
    font-size: 16px;
}

.checkboxes input:nth-child(3){
    margin-left: 30px;
}

.checkboxes input{
    width: 16px;
    height: 16px;
}

.checkboxes label{
    margin-left: 5px;
    font-size: 18px;
}

.visibleBtn{
    background: transparent;
    border: 1px solid transparent;
}