
.header{
    width: 100%;
    height: 10vh;
    border: 1px solid rgba(128,128,128,0.5);
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_button{
    background-color: #2563eb;;
    color: white;
    outline: none;
    border: none;
    line-height: 1.5rem;
    font-size: 1rem;
    padding: 0.25rem 0.75rem;
    border-radius: 0.25rem;
}

.img_container{
    width: 90px;
    height: 32px;
    background-image: url("https://res.cloudinary.com/practicaldev/image/fetch/s--WcWx6Fef--/c_imagga_scale,f_auto,fl_progressive,h_420,q_auto,w_1000/https://dev-to-uploads.s3.amazonaws.com/i/f3mn2sw6is59zrxhzj51.png");
    background-size: cover;
    background-position: center;
    background-color: red;
    border-radius: 0.25rem;
    cursor: pointer;
}

.header_right{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.header_text{
    font-size: 22px;
    font-weight: 600;
}


