.displayWrapper{
    position: absolute;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    inset: 0;
    background: rgb(0,0,0,0.5);
    z-index: 100;
}

.displayFinal{
    padding: 20px;
    border-radius: 0.5rem;
    width: 45%;
    height: auto;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    background: white;
    gap: 20px;
}

.displayer{
    display: flex;
    width: 100%;
    justify-content: space-between;
    gap: 10px;
}

.resultDisplay{
    width: 100%;
    /*border: 1px solid black;*/
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    padding: 15px;
    border-radius: 0.5rem;
    background-color: rgb(243, 244, 246);
}

.displayer button{
    padding: 15px;
    font-size: 18px;
    border-radius: 0.5rem;
    outline: none;
    border: none;
}

.displayer span{
    color: blue;
    margin-right: 5px;
}
.closeDiv{
    display: flex;
    justify-content: flex-end;
}

.closeDiv button{
    padding: 10px 20px;
    font-size: 18px;
    color: white;
    background: #2563eb;
    border: none;
    outline: none;
    border-radius: 0.5rem;
}